  <div class="content-box">
    <div class="container" style="margin-top:0px">
      <div style="margin-left: 10px">
        <el-breadcrumb separator-class="el-icon-arrow-right">
          <el-breadcrumb-item :to="{ path: '/test6/test6-1' }">企业数据查询</el-breadcrumb-item>
          <el-breadcrumb-item>企业异常数据</el-breadcrumb-item>
        </el-breadcrumb>
      </div>
      <div style="margin-top: 15px;font-weight: 700;color: #303133">
      <el-form :inline="true" :model="slectmodel" class="demo-form-inline">
        <el-form-item label="企业异常数据">
          <el-select v-model="data1" placeholder="请选择查询类型">
            <el-option label="客运" value="客运"></el-option>
            <el-option label="危运" value="危运"></el-option>
            <el-option label="总览" value="总览"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="onSubmit">查询</el-button>
        </el-form-item>
        <el-form-item label="企业异常报告">
          <el-select v-model="formInline.report" placeholder="请选择查询类型">
            <el-option label="客运" value="客运"></el-option>
            <el-option label="危运" value="危运"></el-option>
            <el-option label="总览" value="总览"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="onSubmitreport">查询</el-button>
        </el-form-item>
      </el-form>
</div>
      <!--数据展示部分-->
      <div class="table-container"style="margin: 5px">
        <el-table :data="tableData" border  fit="true" >
          <el-table-column fixed prop="id" label="编号" width="80" align="center" sortable></el-table-column>
          <el-table-column prop="licenseplate" label="车牌号码" width="300" align="center"></el-table-column>
          <el-table-column prop="licenseplatecolor" label="车牌颜色" width="120" align="center"></el-table-column>
          <el-table-column prop="name" label="所属企业" width="350" align="center"></el-table-column>
          <el-table-column prop="type" label="所属行业" width="150" align="center"></el-table-column>
          <el-table-column prop="adress" label="管辖机构" width="250" align="center"></el-table-column>
          <el-table-column prop="location" label="车籍所在地" width="250" align="center"></el-table-column>
          <el-table-column prop="status" label="经营状态" width="120" align="center"></el-table-column>
          <!--      最后一行加入两个按钮，配置两个点击事件 编辑和删除-->
          <el-table-column label="操作" width="100" align="center" fixed="right">

            <template slot-scope="scope">
              <div style="display: inline-block">
                <!--修改按钮-->
                <!--<el-button type="mini" size="mini" @click="edit(scope.row)">修改</el-button>-->
                <div style="display: inline-block">
                  <el-button type="text" size="mini" plain @click="edit(scope.row)" >编辑</el-button>
                  <!--<modify-dialog :dialogVisible="isShow" @close="closeDialog"></modify-dialog>-->
                </div>
                <!--删除按钮-->
                <el-button  size="mini"  type="text" class="delete-button" slot="reference" @click="deleteid(scope.row)">删除</el-button>
              </div>
            </template>
          </el-table-column>
        </el-table>
        <el-pagination
          @current-change="page"
          :current-page="currentPage4"
          :page-size="17"
          layout="total,prev, pager, next, jumper"
          :total="1000">
        </el-pagination>
      </div>
      <!--分页组件-->
      <!-- page-size每页展示的记录数-->
      <!--创建点击事件@currentchange="page"-->
<!--      <div class="table-container"style="margin: 5px">-->
<!--        <el-table :data="tableData" border  fit="true" >-->
<!--          <el-table-column fixed prop="id" label="编号" width="80" align="center" sortable></el-table-column>-->
<!--          <el-table-column prop="licenseplate" label="车牌号码" width="350" align="center"></el-table-column>-->
