<!--          <el-table-column prop="type" label="所属行业" width="150" align="center"></el-table-column>-->
<!--          <el-table-column prop="adress" label="管辖机构" width="250" align="center"></el-table-column>-->
<!--          <el-table-column prop="location" label="车籍所在地" width="250" align="center"></el-table-column>-->
<!--          <el-table-column prop="status" label="经营状态" width="120" align="center"></el-table-column>-->
<!--          &lt;!&ndash;      最后一行加入两个按钮，配置两个点击事件 编辑和删除&ndash;&gt;-->
<!--          <el-table-column label="操作" width="100" align="center" fixed="right">-->

<!--            <template slot-scope="scope">-->
<!--              <div style="display: inline-block">-->
<!--                &lt;!&ndash;修改按钮&ndash;&gt;-->
<!--                &lt;!&ndash;<el-button type="mini" size="mini" @click="edit(scope.row)">修改</el-button>&ndash;&gt;-->
<!--                <div style="display: inline-block">-->
<!--                  <el-button type="text" size="mini" plain @click="edit(scope.row)" >编辑</el-button>-->
<!--                  &lt;!&ndash;<modify-dialog :dialogVisible="isShow" @close="closeDialog"></modify-dialog>&ndash;&gt;-->
<!--                </div>-->
<!--                &lt;!&ndash;删除按钮&ndash;&gt;-->
<!--                <el-button  size="mini"  type="text" class="delete-button" slot="reference" @click="deleteid(scope.row)">删除</el-button>-->
<!--              </div>-->
<!--            </template>-->
<!--          </el-table-column>-->
<!--        </el-table>-->
<!--        <el-pagination-->
<!--          @current-change="page"-->
<!--          :current-page="currentPage4"-->
<!--          :page-size="17"-->
<!--          layout="total,prev, pager, next, jumper"-->
<!--          :total="1000">-->
<!--        </el-pagination>-->
<!--      </div>-->

    </div>
    <!--数据展示部分-->
    <!--分页组件-->
    <!-- page-size每页展示的记录数-->
    <!--创建点击事件@currentchange="page"-->


  </div>

</template>


<script>
// import Modifydialog from '@/components/dialog/modify.vue';
export default {
// 引入组件
// components:{
//   'modify-dialog': Modifydialog
//   },
  // total定义总数,null定义空值，由后端读取数据
  data(){
    return {
   formInline: {
        data: '',
        report: ''
       },
      data1:'',
      input:null,
      total:null,
      tableData:null,
      dialogTableVisible: false,
      dialogFormVisible: false,
      modifydialogVisible: false,
      // isShow:false,
      currentPage4:1,
      // 获取对象 与form的model绑定，对象放入data中 对象与表单绑定
      ruleForm: {
        id:'',
        licenseplate:'',
        licenseplatecolor:'',
        name: '',
        type: '',
        adress: '',
        location:'',
        status: '',
      },
// 表单验证规则 放在data中,与表单rules绑定
//       rules: {
//         name: [
//           { required: true, message: '请输入姓名', trigger: 'blur' },
//           { min: 3, max: 5, message: '长度在 3 到 5 个字符', trigger: 'blur' }],
//         type: [{ required: true, message: '请选择行业类型', trigger: 'change' }],
//         adress: [{ required: true, message: '请输入管辖机构', trigger: 'change' }],
//         status: [{ required: true, message: '请选择运行状态', trigger: 'change' }]
//       }

    }
  },

  methods: {
    onSubmitreport(){
      console.log(1)
    },
    onSubmit() {
     if (this.data1 === '陕AD4516'){
       this.tableData=[{
         id:'1',
         licenseplate:'陕AD4516',
         licenseplatecolor:'lvse',
         name: '陕西西安',
         type: '重载普货',
         adress: '烽火平台',
         location:'西安',
         status: '营业',

       }]
     }
    },
// 提交 数据函数 立即创建submitForm
    submitForm() {
      // _this表示当前vue对象
      const _this = this;
      // 表单所有数据通过校验 传
      this.$refs['ruleForm'].validate((valid) => {
        if (valid) {
          // 校验成功发送数据post this.ruleForm传送的是对象，直接在url后边加
          this.$axios.post('http://localhost:8181/book/save',this.ruleForm).then(function (resp) {
            // alert('123')
            // 传送完成后后台返回success或error 对其进行判断，显示提示信息
            if(resp.data == 'success'){
              _this.$alert('《'+_this.ruleForm.name+'》创建成功！', '消息', {
                confirmButtonText: '确定',
                // callback回调函数 调回原界面
                callback: action => {
                  // 路由，跳转页面 调到原页面 push指令
                  _this.$router.push('/test2/test2-1')
                }
              })
            }
          })
        }else {
          return false;
        }
      })
    },

// 重置数据界面的函数 重置resetForm
    resetForm(formName) {
      // 获取表单并全部置空
      this.$refs[formName].resetFields();
    },

// 删除数据 直接调后端删除
    deleteid(row){
// 消息框提示
      this.$confirm('确定删除该条记录吗?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        const _this=this;
        this.$axios.delete('http://localhost:8181/book/deleteById/'+row.id).then(function (resp) {
// 前端自带动态刷新界面
          window.location.reload()
        });
        this.$message({
          type: 'success',
          message: '删除成功!'
        });
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消删除'
        });
      });
    },

// 修改数据 编辑 传到后台 第一步先调到前端页面
    edit(row) {
      // 跳转页面到modify 不重新定义 this 数据读出并自动赋值 modify与test2-1大致相同
      this.$router.push({
        // 要跳页面的路径
        path:'/modify',
        // 参数 传送所选行的id
        query:{
          id:row.id
        }
      })
    },

// 页面
    page(currentPage) {
      const _this=this;
      this.$axios.get('http://localhost:8181/book/findAll/'+currentPage+'/17').then(function (resp) {
        //  console.log(resp.data)
        _this.tableData=resp.data.content;
        _this.total=resp.data.totalElements;

      })
    },
  },
// 取数据
  created(){
    const _this=this;
    // "'http://localhost:8181/book/findAll/{page}/{size}"
    this.$axios.get('http://localhost:8181/book/findAll/1/17').then(function (resp) {
      console.log(resp.data)
      _this.tableData=resp.data.content;
      _this.total=resp.data.totalElements
    })
  }
}
</script>

<style>
/* 设置当前选中行的背景颜色 */
.el-table__body tr.current-row>td {
  background: #c9eff5 !important;
}
/* 设置table header的背景颜色 */
.el-table__header th, .el-table__header tr {
  background-color: #17B3A3;
  color: black;
}
/* 设置表主体的高度 */
.el-table__body td,.el-table__body th{
  padding:3px;
}
/* 设置表头的高度 */
.el-table__header td,.el-table__header th{
  padding:6px 0px;
}
/* 设置分页器的高度 */
.site-wrapper .el-pagination {
  margin-top: 5px;
  text-align: right;
}
.el-pager li.active {
  color: #080909;
  cursor: default;
  background-color: #17B3A3;
  border-radius: 2px;
}
.delete-button{
  color: #ee3441;
}
.delete-button:hover{
  color: #ee9509;
}
</style>



